<template>
<div class="modal" tabindex="-1" data-bs-backdrop="static">
  <div :class="modalClass">
    <div class="modal-content">
      <header class="modal-header">
        <h5 class="modal-title">{{title}}</h5>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" @click="$emit('modal:close')"></button>
      </header>
      <div class="modal-body">
        <slot>
          <p>Modal body text goes here.</p>
        </slot>
      </div>
      <footer class="modal-footer">
        <slot name="action">
        </slot>
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" @click="$emit('modal:close')">Fermer</button>
      </footer>
    </div>
  </div>
</div>
</template>
<script>
export default {
  name: 'Modal',
  props: {
    title: String,
    size: {
      type: String,
      default: 'lg',
    }
  },
  computed: {
    modalClass() {
      let size = `modal-${this.size}`
      return ['modal-dialog', 'modal-dialog-scrollable', size]
    },
  },
  emits: ['modal:close'],
  methods: {
    toggle() {
      let modal = window.bootstrap.Modal.getOrCreateInstance(this.$el)
      modal.toggle()
    }
  }
}
</script>
