<template>
  <Modal ref="modal" :title="'détail de la commande '+$route.params.id" @modal:close='done'>
    <template v-slot:default>
      <OrderDetail :products="order.products"/>
      <div class="container">
        <div class="row">
          <div class="col" v-if="order && order.contact">
            <form>
              <div class="mb-3">
                <label>client</label>
                <input type="text" class="form-control" readonly :value="order.contact.name">
              </div>
              <div class="mb-3">
                <label>e-mail</label>
                <input type="email" class="form-control" readonly :value="order.contact.email || 'indisponible'">
              </div>
              <div class="mb-3">
                <label>telephone</label>
                <input type="text" class="form-control" readonly :value="order.contact.phone || 'indisponible'">
              </div>
            </form>
          </div>
          <div class="col" v-if="order">
            <form v-if="order.address">
              <div class="mb-3">
                <label>adresse</label>
                <input type="text" class="form-control" readonly :value="order.address.street">
              </div>
              <div class="mb-3">
                <label>ville</label>
                <input type="text" class="form-control" readonly :value="order.address.city">
              </div>
              <div class="mb-3">
                <label>pays</label>
                <input type="text" class="form-control" readonly :value="order.address.country">
              </div>
            </form>
            <div v-else>
              <p>la commande sera retirée par le client</p>
            </div>
          </div>
          <div class="col" v-if="order">
            <form>
              <div class="mb-3">
                <label>date de création</label>
                <input type="text" class="form-control" readonly :value="order.created">
              </div>
              <div class="mb-3">
                <label>date de livraison/retrait</label>
                <input type="text" class="form-control" readonly :value="order.when">
              </div>
              <div class="mb-3">
                <label>clôturée</label>
                <input type="text" class="form-control" readonly :value="order.closed ? 'cloturé' : 'en traitement'">
              </div>
            </form>
          </div>
        </div>
      </div>
    </template>
    <template v-slot:action>
      <button v-if="!order.closed" type="button" class="btn btn-warning">clôturer</button>
    </template>
  </Modal>
</template>
<script>
import {mapGetters} from 'vuex'
import _ from 'lodash'
import Modal from '../components/Modal.vue'
import OrderDetail from '../components/OrderDetail.vue'

export default {
  name: 'EditOrder',
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.resetDefault()
      vm.toggle()
    })
  },
  data() {
    return {
      order: {},
    }
  },
  computed: {
    ...mapGetters('orders', ['orders']),
  },
  methods: {
    toggle() {
      this.$refs.modal.toggle()
    },
    done() {
      this.$router.go(-1)
    },
    resetDefault() {
      this.order = _.cloneDeep(this.orders[this.$route.params.id])
    },
  },
  components: {
    Modal,
    OrderDetail,
  }
}
</script>
<style scoped>
input[readonly]{
  background: white;
}
</style>
