<template>
  <table :class="['table', {'table-striped': products && products.length > 1}]">
    <thead>
      <tr>
        <th class="text-center">#</th>
        <th>produit</th>
        <th class="text-center">quantité</th>
        <th class="text-center">unité</th>
        <th class="text-end">total</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(p, i) in products" :key="i">
        <td class="text-center">{{i+1}}</td>
        <td>{{p.product}}</td>
        <td class="text-center">{{p.quantity}}</td>
        <td class="text-center">kg</td>
        <td class="text-end">{{p.quantity*p.price}}&euro;</td>
      </tr>
    </tbody>
    <tfoot>
      <tr>
        <th colspan="2">total</th>
        <th class="text-center">{{quantity}}</th>
        <th class="text-center"></th>
        <th class="text-end">{{total}}&euro;</th>
      </tr>
    </tfoot>
  </table>
</template>
<script>
export default {
  name: 'OrderDetail',
  props: {
    products: Array,
  },
  computed: {
    total() {
      return (this.products || []).reduce((p, c) => p + (c.quantity*c.price), 0)
    },
    quantity() {
      return (this.products || []).reduce((p, c) => p+c.quantity, 0)
    }
  }
}
</script>
